import React, { Component } from "react";
import { Input } from "mdbreact";
import { Button, Card, CardHeader, CardBody } from "mdbreact";
import { FormInline, MDBIcon } from "mdbreact";
import { Breadcrumb, BreadcrumbItem } from "mdbreact";

class BoardWrite extends Component {
  render() {
    return (
      <React.Fragment>
        <CardBody
          id="breadcrumb"
          className="d-flex align-items-center justify-content-between"
        >
          <Breadcrumb>
            <BreadcrumbItem active>
              <a href="/board/free"></a>
            </BreadcrumbItem>
            <BreadcrumbItem>
              <a href="/board/free"></a>
            </BreadcrumbItem>
          </Breadcrumb>
          <FormInline className="md-form m-0" />
        </CardBody>
        <Card className="mb-5">
          <CardHeader>
            <FormInline className="md-form m-0">
              <Input
                label=" ."
                icon="pencil"
                group
                type="text"
                validate
                error="wrong"
                success="right"
              />
            </FormInline>
          </CardHeader>
          <CardBody>
            <form className="mx-3 grey-text">
              <textarea
                id="write"
                className="form-control form-control-sm"
                style={{ minHeight: "500px" }}
                placeholder="  3000  ."
              />
            </form>
          </CardBody>
          <div>
            <Button color="dark"> </Button>
            <Button color="dark" href="/board/free">
              
            </Button>
          </div>
        </Card>
      </React.Fragment>
    );
  }
}

export default BoardWrite;
