import React, { Component } from "react";
import {
  Row,
  Col,
  Card,
  Table,
  TableBody,
  TableHead,
  ListGroup,
  ListGroupItem,
  CardBody
} from "mdbreact";
import { Breadcrumb, BreadcrumbItem } from "mdbreact";
import Footerlogo from "../../img/footerlogo.jpg";
import VSlogo from "../../img/VS.png";
import "./TeamVersus.css";
class TeamVersus extends Component {
  constructor(props) {
    super();
    this.state = {
      tech: ""
    };
  }
  handleChange(e) {
    this.setState({
      tech: e.target.value
    });
  }
  render() {
    return (
      <React.Fragment>
        <h3>
          <Breadcrumb>
            <BreadcrumbItem></BreadcrumbItem>
            <BreadcrumbItem></BreadcrumbItem>
          </Breadcrumb>
        </h3>
        <Row>
          <Col md="3">
            <Card>
              <h4>
                <div className="sub_title">
                  <CardBody></CardBody>
                </div>
              </h4>
              <ListGroup>
                <ListGroupItem href="/record/rank"> </ListGroupItem>
                <ListGroupItem href="/record/versus" active>
                  
                </ListGroupItem>
                <ListGroupItem href="#"> </ListGroupItem>
                <ListGroupItem href="#"> </ListGroupItem>
              </ListGroup>
            </Card>
          </Col>
          <Col md="9">
            <Card className="mb-5">
              <div classNmae="mt-1">
                <h4> </h4>
              </div>
              <Row>
                <Col>
                  <div id="cardleftbottom">
                    <Row>
                      <Col>
                        <div id="hometeam">
                          <div>
                            <img src={this.state.tech} />
                          </div>
                          <select
                            className="browser-default custom-select"
                            onChange={this.handleChange.bind(this)}
                            value={this.state.tech}
                          >
                            <option value={Footerlogo}> </option>
                            <option value={Footerlogo}> </option>
                            <option value="1"> KCC</option>
                            <option value="1"> </option>
                            <option value="1"> </option>
                            <option value="1"> </option>
                            <option value="1"> LG</option>
                            <option value="1"> SK</option>
                            <option value="1"> KT</option>
                            <option value="1"> KGC</option>
                            <option value="1"> DB</option>
                          </select>
                        </div>
                      </Col>
                      <Col>
                        <div id="vsimg">
                          <img src={VSlogo} width="71px" height="58px" />
                        </div>
                      </Col>
                      <Col>
                        <div id="awayteam">
                          <img src={Footerlogo} width="71px" height="58px" />
                          <select className="browser-default custom-select">
                            <option>  </option>
                            <option value="1"> </option>
                            <option value="1"> KCC</option>
                            <option value="1"> </option>
                            <option value="1"> </option>
                            <option value="1"> </option>
                            <option value="1"> LG</option>
                            <option value="1"> SK</option>
                            <option value="1"> KT</option>
                            <option value="1"> KGC</option>
                            <option value="1"> DB</option>
                          </select>
                        </div>
                      </Col>
                    </Row>
                  </div>
                  <div id="seasonrecordtable">
                    <Table>
                      <tr>
                        <td>Mark</td>
                        <td></td>
                        <td>@mdo</td>
                      </tr>
                      <tr>
                        <td>Jacob</td>
                        <td></td>
                        <td>@fat</td>
                      </tr>
                      <tr>
                        <td>Larry</td>
                        <td></td>
                        <td>@twitter</td>
                      </tr>
                    </Table>
                  </div>
                  <div />
                </Col>

                <Col>
                  <div id="datetable">
                    <Table>
                      <TableHead>
                        <tr>
                          <th></th>
                          <th>/</th>
                          <th></th>
                          <th></th>
                          <th>/</th>
                        </tr>
                      </TableHead>
                      <TableBody>
                        <tr>
                          <td>2018.10.13</td>
                          <td></td>
                          <td>90</td>
                          <td>80</td>
                          <td></td>
                        </tr>
                      </TableBody>
                    </Table>
                  </div>
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default TeamVersus;
