import React from "react";
import { Button, CardImage, CardTitle, CardText, CardBody } from "mdbreact";
import { Row, Col, CardHeader } from "mdbreact";
import axios from "axios";

class TeamIntro extends React.Component {
  constructor(props) {
    super(props);
    this.state = { text: {} };
  }
  componentDidMount() {
    this.TeamIntro();
  }
  TeamIntro() {
    const tCode = this.props.tCode;
    return axios
      .get("https://rbd.javajs.net:8100/team/" + tCode, {})
      .then(res => {
        const text = res.data;
        this.setState({ text });
      });
  }
  render() {
    return (
      <React.Fragment>
        <CardHeader>
          <CardTitle></CardTitle>
        </CardHeader>
        <CardBody>
          <Row>
            <Col>
              <Row>
                <Col>
                  <CardImage
                    className="img-fluid mx-auto"
                    src={this.props.teamImg}
                    waves
                  />
                </Col>
                <Col>
                  <CardTitle>{this.state.text.team_name}</CardTitle>
                  <CardText>
                     : {this.state.text.team_owner}
                    <br />
                      : {this.state.text.team_reowner}
                    <br />
                     : {this.state.text.team_leader}
                    <br />
                     : {this.state.text.team_director}
                    <br />
                     : {this.state.text.team_coach}
                    <br />
                     : {this.state.text.team_address}
                    <br />
                    / : {this.state.text.team_company}
                    <br />
                  </CardText>
                  <Button href="#">  </Button>
                </Col>
              </Row>
            </Col>
          </Row>
        </CardBody>
      </React.Fragment>
    );
  }
}
export default TeamIntro;
